html.dark{
  background:#28293D
}

html.light{
  background:#f6f6f6
}

*:focus{
  outline:0 !important
}

@media (min-width: 640px) {
}

@media (min-width: 768px) {
  .md\:block {
    display: block;
  }

  .md\:text-3xl {
    font-size: 1.875rem;
    line-height: 3rem;
  }

  .md\:text-6xl {
    font-size: 3.75rem;
    line-height: 5rem;
  }

  .md\:mt-12 {
    margin-top: 3rem;
  }

  .md\:mt-16 {
    margin-top: 4rem;
  }

  .md\:mb-24 {
    margin-bottom: 6rem;
  }

  .md\:p-3 {
    padding: 0.75rem;
  }

  .md\:py-4 {
    padding-top: 1rem;
    padding-bottom: 1rem;
  }

  .md\:px-8 {
    padding-left: 2rem;
    padding-right: 2rem;
  }

  .md\:px-12 {
    padding-left: 3rem;
    padding-right: 3rem;
  }

  .md\:gap-12 {
    gap: 3rem;
  }

  .md\:grid-cols-2 {
    grid-template-columns: repeat(2, minmax(0, 1fr));
  }

  .md\:grid-cols-3 {
    grid-template-columns: repeat(3, minmax(0, 1fr));
  }

  .md\:grid-cols-4 {
    grid-template-columns: repeat(4, minmax(0, 1fr));
  }
}

